<template>
	<div class="vAlert" v-if="alertShow" @click="tap">
		<div class="mask"></div>
		<div class="content">
			<h3 class="title">{{ title }}</h3>
			<div class="con">
				<slot>
					<!--只有在没有要分发的内容时才会显示。-->
				</slot>
			</div>
			<div class="bottom">
				<!--<button type="button" class="btn btn-primary">确定</button>-->
				<div>（任意区域关闭）</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			title: {
				default: '提示'
			},
			alertShow: {
				default: false
			},
		},
		data () {
			return {
			}
		},
		created () {
		},
		mounted () {
		},
		computed: {},
		watch: {},
		methods: {
			tap () {
				this.$emit('tap')
			},
		}
	}
</script>

<style lang="stylus" scoped>
	.vAlert{
		position: fixed;
		top:0;
		left:0;
		z-index: 1050;
		display: block;
		width:100%;
		height:100%;

		.mask{
			position: absolute;
			width:100%;
			height:100%;
			background-color: rgba(0, 0, 0, 0.5);
		}

		.content{
			width:70%;
			height:200px;
			padding: 50px 0;
			position: absolute;
			top:50%;
			left:50%;
			transform: translate(-50%, -50%);
			background-color: #fff;
			border-radius: 4px;
			overflow: hidden;

			.title{
				position: absolute
				top: 0
				margin: 0;
				padding: 0 20px;
				font-size: 18px;
				width: 100%
				height:50px;
				line-height:50px;
				border-bottom: 1px solid #ccc;
			}

			.con{
				overflow-y: auto;
				height: 100%
				max-height: 300px;
				line-height:26px
				text-align center
				padding 10px
			}

			.bottom{
				width:100%;
				height:50px;
				position: absolute;
				bottom:0;
				border-top: 1px solid #ccc;
				text-align: right;

				.btn{
					margin: 8px 10px;
				}
				
				div{
					line-height:50px
					text-align center
					color: #999
				}
			}
		}
	}
</style>



