
<template>
	<div class="addacc">
		<div class="row">
			<div class="col-md-12" style="padding: 10px">
				<button type="button" class="file btn btn-default">选择文件
					<input type="file" accept="text/plain" value="选择文件" @change="fileLoad"></button>
				<button type="button" class="btn btn-default" @click="clearAcc">清除</button>
			</div>
		</div>
		<div class="areaBox row">
			<textarea v-model="accStr" class="textarea" rows="10"></textarea>
			<button type="button" class="add btn btn-default" @click="addAcc">添加</button>
		</div>
		<div v-if="accList.length" class="row">
			<div v-if="li" v-for="li in accList">{{ li['acc'] }} - {{ li['pwd'] }} - {{ li['status'] }}</div>
		</div>
		
		<vAlert @tap="tap" :alertShow="alertShow" class="vAlert">
			<div>{{ alertStr }}</div>
		</vAlert>
	</div>
</template>

<script>
	import vAlert from './vAlert.vue'
	import formatAcc from './formatAcc'
	export default {
		components: {
			vAlert
		},
		data () {
			return {
				txt: '',
				fileName: '',
				accStr: '',
				alertShow: false,
				alertStr: '',
			}
		},
		beforeCreate () {
			// 判断浏览器是否支持FileReader接口
			if (typeof FileReader === 'undefined') {
				alert("浏览器不支持！")
			}
		},
		mounted () {
		},
		computed: {
			accList () {
				return this.$store.state.accList
			}
		},
		methods: {
			fileLoad (e) {
				let file = e.target.files[0]
				let reader = new FileReader()
				let txt
				
				this.fileName = file.name
				reader.readAsText(file)
				reader.onloadend = (f) => {
					txt = f.target.result
					this.$store.commit('saveAccList', formatAcc(txt))
					this.alertStr = '添加成功'
					this.alertShow = true
				}
			},
			addAcc () {
				this.$store.commit('saveAccList', formatAcc(this.accStr))
				this.accStr = ''
			},
			clearAcc () {
				this.$store.commit('clearAccList')
			},
			tap () {
				console.log(11)
				this.alertShow = false
			},
		}
	}
</script>

<style lang="stylus" scoped>
	.btn{
		outline:none !important
		
		&:active{
			background-color #fff !important
		}
		&:hover{
			background-color #fff !important
		}
	}
	.file{
		position: relative
		overflow hidden
		
		input{
			position: absolute
			top 0
			left: 0
			width:100%
			height:100%
			opacity 0
		}
	}
	
	.areaBox{
		position: relative
		overflow: hidden
		.add{
			position: absolute
			right:1px
			bottom:1px
			border-radius 0
			border:none
			box-shadow 0 0 3px 0 #f5c607
		}
	}
	.textarea{
		width:100%
		font-size 16px
		border:none
		box-shadow 0 0 3px 0 #f5c607 inset
		box-sizing border-box
		display: block
	}
</style>



