<template>
	<div id="app" class="container-fluid">
		<div class="content row">
			<nav>
				<router-link to="/vote" class="item" tag="div">打码要认真</router-link>
				<router-link to="/notice" class="item" tag="div">通知要勤看</router-link>
			</nav>
			<div class="content col-md-12">
				<keep-alive>
					<router-view></router-view>
				</keep-alive>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'app',
		beforeCreate () {
			this.$store.commit('readAccList')

			if (!localStorage.getItem('userId')) {
				axios.get('http://mmvote.viphk.ngrok.org/api/userId')
					.then(res => {
						if (res.data.code === 0) {
							this.$store.commit('userId', res.data.data)
							localStorage.setItem('userId', JSON.stringify(res.data.data))
						}
					})
			}
			else {
				this.$store.commit('userId', JSON.parse(localStorage.getItem('userId')))
			}
		}
	}
</script>

<style lang="stylus" scoped>
	nav{
		display: flex
		text-align center
		.item{
			flex:1
			height:40px
			line-height:40px
			background-color #f5c607
			color: #fff

			&.active{
				background-color #fff
				color: #000
			}
		}
	}
</style>



