
module.exports = function (str) {
	return str.trim().split('\n').map(item => {
		let acc = item.trim().split(/ +/g)
		return {
			acc: acc[0],
			pwd: acc[1],
			JSESSIONID: acc[2],
			wim_userInfo: acc[3],
			status: acc[4],
		}
	})
}



