import Vue from 'vue'
import Router from 'vue-router'
// import App from './App.vue'
import addacc from './components/addacc.vue'

Vue.use(Router)

export default new Router({
	linkActiveClass: 'active',
	routes: [
		{
			path: '/',
			redirect: '/addacc',
			children: [
			]
		},
		{
			path: '/vote',
			component: resolve => {
				require.ensure(['./components/vote.vue'], () => {
					resolve(require('./components/vote.vue'))
				})
			},
		},
		{
			path: '/notice',
			component: resolve => {
				require.ensure(['./components/notice.vue'], () => {
					resolve(require('./components/notice.vue'))
				})
			},
		},
		{
			path: '/addacc',
			component: addacc,
		},
	]
})



