export default {
	saveAccList (state, data) {
		if (!(state.accList instanceof Array)) {
			state.accList = []
		}
		state.accList = state.accList.concat(data)
		localStorage.setItem('accList', JSON.stringify(state.accList))
	},
	clearAccList (state) {
		state.accList = []
		localStorage.setItem('accList', JSON.stringify(state.accList))
	},
	readAccList (state) {
		state.accList = JSON.parse(localStorage.getItem('accList')) ? JSON.parse(localStorage.getItem('accList')) : []
	},
	userId (state, data) {
		state.userId = data
	},
}



